import * as Papa from 'papaparse';
import * as dayjs from 'dayjs';

//https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_daily_reports/03-02-2020.csv

export default class Lib {


    get FILES_LIST_URL(){
        return `https://api.github.com/repos/CSSEGISandData/COVID-19/contents/csse_covid_19_data/csse_covid_19_daily_reports`;
    }

    async getLatestFile(){
        let dd = await fetch(this.FILES_LIST_URL);
        let files_list = JSON.parse(await dd.text());
        let _last_file = {
            download_url: ""
        };
        for(let i=files_list.length-1;i>0;i--){
            if(files_list[i].name.indexOf('csv')>-1){
                _last_file = files_list[i];
                break;
            }
        }
        return _last_file.download_url;
    }

    static get instance(){
        return window['geo_tool_1'] ? window['geo_tool_1'] : (()=>{
            window['geo_tool_1'] = new Lib();
            return window['geo_tool_1'];
        })();
    }

    distance(UserLat, userLong, caseLat, caseLong, unit) {
        if ((UserLat == caseLat) && (userLong == caseLong)) {
            return 0;
        }
        else {
            var radUserLat = Math.PI * UserLat / 180;
            var radcaseLat = Math.PI * caseLat / 180;
            var theta = userLong - caseLong;
            var radtheta = Math.PI * theta / 180;
            var dist = Math.sin(radUserLat) * Math.sin(radcaseLat) + Math.cos(radUserLat) * Math.cos(radcaseLat) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = dist * 180 / Math.PI;
            dist = dist * 60 * 1.1515;
            if (unit == "K") { dist = dist * 1.609344 }
            if (unit == "N") { dist = dist * 0.8684 }
            return dist;
        }
    }

    async hasGeoLocationActivated(){
        if(navigator.permissions){
            return await new Promise((accept)=>{
                navigator.permissions.query({name: 'geolocation'}).then(function(PermissionStatus) {
                    accept(PermissionStatus.state == 'granted');
                })
            });
        }
        return null;
    }

    async fetchLocation() {
        if ("geolocation" in navigator) {
            /* la geolocalizaci칩n est치 disponible */
            let res = await new Promise((accept) => {
                /**
                 *  latitude: 10.4963315
                    longitude: -66.877371
                    altitude: null
                    accuracy: 26
                    altitudeAccuracy: null
                    heading: null
                    speed: null
                 */
                navigator.geolocation.getCurrentPosition(accept);
            });
            return {
                coords: [res.coords.latitude, res.coords.longitude],
                accuracy: res.coords.accuracy
            };
        } else {
            /* la geolocalizaci칩n NO est치 disponible */
            window.alert("Gelocation not available for this device");
        }
    }

    async getNextUrl(){

    }

    async fetchData() {
        let data = await new Promise(async (accept) => {
            let download_url = await this.getLatestFile();
            let report_date = download_url.split("/").pop().split(".")[0];
            report_date = report_date.replace(/-/g, '/').split("/").reverse();
            report_date = dayjs(report_date).toDate().toLocaleDateString();

            Papa.parse(download_url, {
                download: true,
                complete: function (results) {
                    accept(results.data.map((element, index) => {
                        if (index < 1) return null;
                        let return_element =  {
                            province_state: element[0],
                            country_region: element[1],
                            last_update: element[2],
                            confirmed: parseInt(element[3]),
                            deaths: parseInt(element[4]),
                            recovered: parseInt(element[5]),
                            latitude: parseFloat(element[6]),
                            longitude: parseFloat(element[7]),
                            report_date
                        };
                        if(return_element.latitude != return_element.latitude) return null;
                        return return_element;
                    }).filter((element) => !!element));
                }
            });
        });
        return data;
    }
}